import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Tube from '../views/SteelTube.vue'
import IBeam from '../views/IBeam.vue'
import CBeam from '../views/CBeam.vue'
import EqAngle from '../views/EqAngle.vue'
import UneqAngle from '../views/UneqAngle.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/tube',
    name: 'Tube',
    component: Tube
  },
  {
    path: '/ibeam',
    name: 'IBeam',
    component: IBeam
  },
  {
    path: '/cbeam',
    name: 'CBeam',
    component: CBeam
  },
  {
    path: '/eqAngle',
    name: 'EqAngle',
    component: EqAngle
  },
  {
    path: '/uneqAngle',
    name: 'UneqAngle',
    component: UneqAngle
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.publicPath,
  routes
})

export default router
